.intro {
  margin-left: 50px;
  margin-top: 90px;
  transition: all 1s ease-in;
  height: 80vh;
}

.hey-im {
  font-size: 40px;
  font-weight: 800;
  transition-timing-function: cubic-bezier(1, 0, 0, 1);
  transition: all 1s ease-in;
}

.i-like {
  font-size: 20px;
  font-weight: 700;
}

.homepage {
  display: flex;
  flex-direction: column;
}

a {
  text-decoration: none;
}
.icon {
  color: black;
  margin: 0px 10px 0px 0px;
  transition: all cubic-bezier(0.47, 0, 0.745, 0.715) 0.1s;
}
.icon:hover {
  color: grey;
}
