.project-item {
  display: flex;
  border: black solid 1px;
  flex-direction: row;
  word-wrap: break-word;
  flex-basis: 100%;
  width: 30em;
}
.image {
  height: 10em;
  width: 10em;
}
.text-section p {
  word-wrap: break-word;
}

.text-section {
  margin: 15px;
  display: flex;
  flex-basis: 100%;
  word-wrap: break-word;
  justify-content: center;
  flex-direction: column;
}
